import inputBox from './../basicInformation/inputBox';
export default {
	components: {
		inputBox
	},
	props: [
		'value',
		'disabled',
		'maxlength',
		'indexNum',
		'dataList',
	],
	data() {
		return {
			dataLists: this.dataList,
		};
	},
	watch: {
		dataList: {
			handler(a, b) {
				console.log('----------------dataList--------------')
				this.dataLists = a;
			},
			deep: true
		},
	},
	created() {},
	methods: {
		tapCenterInput (e) {
			console.log(e, '用户行为');
			this.dataLists.map((items, index)=>{
				if (this.indexNum === index) {
					items.key = e;
				}
			})
			this.$emit('tap-EditPusrchase', this.dataLists);
		},
	}
};
